import React, { useEffect, useState } from 'react';
import { HashRouter as Router, Route, Routes, Navigate, useLocation } from 'react-router-dom';
import MaxFibra from './pages/MaxFibra';
import VirTelecom from './pages/VirTelecom';
import ReisServices from './pages/ReisServices';
import Contact from './pages/Contact';
import Cadastro from './pages/Cadastro';
import Sidebar from './components/Sidebar';
import ConsultaCpf from './pages/ConsultaCpf';
import ConsultaCnpj from './pages/ConsultaCnpj';
import Login from './pages/Login';
import Home from './pages/Home';
import CustomContextMenu from './components/CustomContextMenu'; // Importe o novo componente
import './App.css';

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(() => {
    return localStorage.getItem('isAuthenticated') === 'true';
  });
  const [contextMenu, setContextMenu] = useState({ isVisible: false, xPos: 0, yPos: 0 });
  const location = useLocation();

  const closeContextMenu = () => {
    setContextMenu({ ...contextMenu, isVisible: false });
  };

  useEffect(() => {
    const isLoginPage = location.pathname === '/' || location.pathname === '/login';
    if (isLoginPage) {
      document.body.classList.add('login-background');
    } else {
      document.body.classList.remove('login-background');
    }
  }, [location]);

  const handleLogin = () => {
    setIsAuthenticated(true);
    localStorage.setItem('isAuthenticated', 'true');
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
    localStorage.removeItem('isAuthenticated');
    window.location.href = '/admin/#/login';
  };

  const showContextMenu = (event) => {
    event.preventDefault();
    if (isAuthenticated) {
      setContextMenu({
        isVisible: true,
        xPos: event.pageX,
        yPos: event.pageY
      });
    }
  };

  const menuOptions = [
    { label: 'Recarregar Página', onClick: () => window.location.reload() },
    { label: 'Sair', onClick: handleLogout }
  ];

  useEffect(() => {
    document.addEventListener('contextmenu', showContextMenu);
    document.addEventListener('click', closeContextMenu);

    return () => {
      document.removeEventListener('contextmenu', showContextMenu);
      document.removeEventListener('click', closeContextMenu);
    };
  }, [isAuthenticated]);

  return (
    <div className={`App ${location.pathname === '/cadastro' ? 'cadastro-page' : ''}`}>
      <Routes>
        {!isAuthenticated ? (
          <Route path="*" element={<Login onLogin={handleLogin} />} />
        ) : (
          <>
            <Route path="/home/*" element={<SidebarLayout onLogout={handleLogout} />} />
            <Route path="*" element={<Navigate to="/home" replace />} />
          </>
        )}
      </Routes>
      <div className="mobile-warning">
        <img src="https://i.ibb.co/g9KDtqK/warning.png" alt="Aviso" />
        <p>O aplicativo está disponível apenas para desktop.</p>
      </div>
      {isAuthenticated && (
        <CustomContextMenu
          xPos={contextMenu.xPos}
          yPos={contextMenu.yPos}
          options={menuOptions}
          isVisible={contextMenu.isVisible}
          closeMenu={closeContextMenu}
        />
      )}
    </div>
  );
};

const SidebarLayout = ({ onLogout }) => (
  <>
    <header className="header">
      <span className="header-title">Dashboard</span>
      <button className="header-button" onClick={onLogout}>Logout</button>
    </header>
    <div className="layout-container">
      <Sidebar onLogout={onLogout} />
      <div className="content">
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/max-fibra" element={<MaxFibra />} />
          <Route path="/max-fibra/consultaCPF" element={<ConsultaCpf />} /> 
          <Route path="/max-fibra/consultaCNPJ" element={<ConsultaCnpj />} />
          <Route path="/vir-telecom" element={<VirTelecom />} />
          <Route path="/reis-services" element={<ReisServices />} />
          <Route path="/contact" element={<Contact />} />
          <Route path="/max-fibra/cadastro" element={<Cadastro />} />
          <Route path="*" element={<Navigate to="/home" replace />} />
        </Routes>
      </div>
    </div>
  </>
);

const Root = () => (
  <Router>
    <App />
  </Router>
);

export default Root;
